* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

.wrap {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80px;
  background-color: rgba(54, 57, 236, 0.7);
}

.search-form {
  display: inline-block;
  position: relative;
}

.search-form > input {
  font-size: 24px;
  padding: 12px 73px 12px 12px;
  margin: 0;
  border-radius: 8px;
  outline: none;
  border: none;
  cursor: pointer;
}

button[type='submit'] {
  top: 50%;
  right: 0%;
  position: absolute;
  transform: translateY(-50%);
  padding: 0;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  outline: none;
  height: 51.6px;
  min-width: 50px;
  background-color: rgb(223, 220, 220);
  font-weight: 700;
  font-size: 16px;
  border: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

button[type='submit']:hover {
  background-color: rgb(191, 189, 189);
}

button[type='submit']:hover .form__icon {
  fill: white;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

.gallery {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.photo-card {
  flex-basis: calc((100% - 90px) / 4);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
  0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
}

.info {
  display: flex;
  justify-content: space-around;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button.load-more {
  margin: 0 auto;
  margin-top: 50px;
  display: block;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
  border: none;
  background-color: rgba(45, 45, 233, 0.731);
  color: white;
  cursor: pointer;
}
button:disabled {
  background-color: #c1c1c1;
  cursor: not-allowed;
}
/* button.hidden {
  display: none;
} */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
