* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.wrap {
  width: 100%;
  height: 80px;
  background-color: rgba(54, 57, 236, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
}

.search-form {
  display: inline-block;
  position: relative;
}

.search-form > input {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  outline: none;
  margin: 0;
  padding: 12px 73px 12px 12px;
  font-size: 24px;
}

button[type="submit"] {
  height: 51.6px;
  min-width: 50px;
  cursor: pointer;
  background-color: #dfdcdc;
  border: rgba(0, 0, 0, 0);
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translateY(-50%);
}

button[type="submit"]:hover {
  background-color: #bfbdbd;
}

button[type="submit"]:hover .form__icon {
  fill: #fff;
}

img {
  width: 100%;
  object-fit: cover;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 100px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 22.5px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

button.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
  border-radius: 15px;
  margin: 50px auto 0;
  padding: 10px 20px;
  font-size: 30px;
  display: block;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

